
.calendar__nav:focus,.calendar__day input:focus+label{z-index:1;outline:2px solid -webkit-focus-ring-color
}
.calendar__nav:active span,.calendar__day label:active span{transform:translate(0, 1px)
}
.calendar{table-layout:fixed;text-align:center;border:1px solid #ccc;-webkit-user-select:none
}
.calendar td,.calendar th{min-width:2em
}
.calendar__month{font-weight:700;text-transform:capitalize
}
.calendar__nav{display:block;width:100%;margin:0;padding:0;border:0;background:none;color:inherit
}
.calendar__nav:hover{background:#eee;-webkit-font-smoothing:antialiased
}
.calendar__nav span{display:inline-block
}
.calendar__nav--prev{transform:scale(-1, 1)
}
.calendar__weekday{font-weight:500
}
.calendar__day{position:relative;vertical-align:middle;font-feature-settings:"lnum" 1
}
.calendar__day input{z-index:-999;position:absolute;left:-9999px;opacity:0
}
.calendar__day input:checked+label{color:#fff;background:#222;-webkit-font-smoothing:antialiased
}
.calendar__day input:checked+label:hover{background:#111
}
.calendar__day label{position:relative;display:block;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.calendar__day label:hover{background:#eee
}
.calendar__day label span{display:inline-block;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.calendar__day--weekend label{background:#f5f5f5
}
.calendar__day--not-in-month label{color:#ccc;background:none
}



